@mixin setFont($alias, $name, $weight: 'normal', $style: 'normal') {
  @font-face {
    font-family: $alias;
    src: url("/fonts/" + $name + ".woff2") format("woff2");
    src: url("/fonts/" + $name + ".ttf") format("ttf");
    font-weight: $weight;
    font-style: $style;
    font-display: swap; } }

@include setFont('Amatic', 'Amatic-Regular', 400);
@include setFont('Amatic', 'Amatic-Bold', 700);
@include setFont('BasisGrotesquePro', 'BasisGrotesquePro-Regular', 400);
@include setFont('BasisGrotesquePro', 'BasisGrotesquePro-Medium', 500);
@include setFont('BasisGrotesquePro', 'BasisGrotesquePro-Bold', 700);
@include setFont('Ambit', 'Ambit-Light', 300);
@include setFont('Ambit', 'Ambit-Regular', 400);
@include setFont('Ambit', 'Ambit-Bold', 600);

@mixin fbasis {
  font-family: 'BasisGrotesquePro', "Arial", sans-serif; }

@mixin fambit {
  font-family: 'Ambit', "Arial", sans-serif; }

@mixin famatic {
  font-family: 'Amatic', "Arial", sans-serif; }

body {
  font-family: 'Amatic', "Arial", sans-serif;
  font-size: 24px;
  line-height: 120%;
  font-weight: 400;
  background-color: $pale-lemon;
  @include smooth-font; }
